<template>
    <van-actionsheet v-model="pShow" title="请输入密码" @cancel="onCanelPay">
      <Pay @finish="onPwdFinish"></Pay>
    </van-actionsheet>
</template>
<script>
//此组件 当时是为了封装一个支付对话框,里面多做一些逻辑,让调用更省事,但目前看需求比较简单不需要再封装,暂时可以忽略这个组件了...
import Pay from './Pay'
import util from '@/common/util'
export default {
  name: 'payActionsheet',
  components: {
    Pay
  },  
  model: {
    prop: 'payShow',
    event: 'evtShow'
  },  
  props: {
    payShow: false,
    goods: [],
    finishUrl: {
      type: String,
      default: "/shop/payresult"
    }
  },
  watch: {
    payShow(newVal, oldVal) {
      this.pShow = newVal;
    },
    pShow(newVal, oldVal) {
      this.$emit('evtShow', newVal)
    }
  },
  data() {
    return {
      pShow: false
    };
  },

  methods: {
    onCanelPay() {
      this.$emit("cancel");
    },
    onPwdFinish(v) {
      if(!this.goods){
        this.$dialog.alert({message: '商品数量为空'});
        return;
      }      
      this.$http.post("order/confirmCart", {
        "did":"device id"
      }).then((res) => {
        //todo
        //成功跳转到支付成功页面
        //钱不足调到充值页面
        //是否提示对话框

        //如果设置了url 就跳转到指定的地址(默认有,想取消设置它为空) 没有设置就触发success事件
        if(this.finishUrl) {
          this.$router.push(this.finishUrl);
        }
        else {
          this.$emit("success");
        }
      }).catch((e)=> {
        let code = util.getErrorCode(e);
        if(code == window.globalData.code.PAYPASSWORD_IS_WRONG) {
          this.$dialog.alert({message: '密码错误'});
        }
        else if(code == window.globalData.code.CART_COIN_NOT_ENOUGH){
          this.$dialog.confirm({message: 'xtoken余额不足,是否去充值?'}).then(()=>{
            // this.$router.push("/xxx/xxx"); //xtoken购买页面
          }).catch(()=> {});
        }
      })      
    }
  },
  mounted () {

  }
}
</script>

